.navbar{
  display: flex;
}
.nav-items{
  list-style: none;
  display: flex;
  font-family: 'Inter', sans-serif;
  padding: 10px;
}

.meme-logo{
  width: 100px;
}
body{
  background: url(./images/photo-1464618663641-bbdd760ae84a.avif);
